import React from 'react';
import classNames from 'classnames/bind';
import {Provider} from 'react-redux';
import { Link } from 'react-router-dom';

import styles from './Browse.module.scss';

import {TaskList} from './TaskList';
import {TaskDetail} from './TaskDetail';
import {store} from './Store';
import * as Action from './Store/Action';

const testData = {
  title: 'Roof repair',
  status: 'open',
  budget: '120',
  poster: {
    name: 'Tifa',
    avatar: "data:image/png;base64,"
  },
  location: 'Kurunjang VIC 3337, Australia',
  due: 'Monday, 24th Aug 2020',
  details: `-remove existing bamboo fence - supply and install new fence (open for ideas)
            - install synthetic turf
            - landscaping design and install
            - remove and upgrade existing waterfall feature into the pool`,
};

function createData(size, data) {
  const dataArray = [];
  for(let i = 0; i < size; i ++) {
    const newData = {...data};
    newData.id = i;
    dataArray.push(newData);
  }
  return dataArray;
}

const dataArray = createData(10, testData);
dataArray[1].title = 'Wall repair';


function Browse() {

  function initializeTaskList(taskList) {
    store.dispatch(Action.initialize(taskList));
  }

  function selectTask(index) {
    store.dispatch(Action.select(index));
  }

  initializeTaskList(dataArray);
  selectTask(0);

  const cx = classNames.bind(styles);

  return(
    <div className = {cx('browse')}>
      <Link className={styles.back_to_home} to="/eureka">&#10007;</Link>
      <Provider store = {store}>
        <TaskList/>
        <TaskDetail/>
      </Provider>
    </div>
  );
}

export default Browse;